/* navbar styling */
.navbar {
  font-family: 'Poppins', sans-serif; 
  background: none;
}

.list {
  list-style-type: none;
  display: inline-flex;
}

/* the about button */
.about.btn-primary  {
  float: right;
  background-color: transparent;
  color: #7B68EE;
  border-color: transparent;
  margin-top: 10px;
}

.about.btn-primary:hover {
  color:#7B68EE;
  background-color: transparent;
  border: 2px solid #7B68EE;
}

.about.btn-primary::after {
  background-color: transparent;
  color: #7B68EE;
  border-color: transparent;
}

/* the name  */
a.navbar-brand.name-logo:hover {
  color:#7B68EE;
  font-size: xx-large;
}

a.navbar-brand.name-logo{
  color: #7B68EE;
  font-size: xx-large;
  padding-left: 20px;
}

.whole {
  font-family: 'Poppins', sans-serif;
  color: darkseagreen;
}

.profile-pic {
  border-radius: 50%;
  float: left;
  width: 200px;
  padding: 2px;
  margin: 0px;
}

.bio-text{ 
  margin: 10px;
  padding: 10px;
  font-size: medium;
}

.btn.btn-secondary {
  border: none;
  background-color:darkseagreen;
  color: white;
  border-radius: 10px;
}
/* landing styling */

.hero {
  max-width: 100%;
}

.hero-image {
  max-width: 100%;
  height: auto;
  z-index: 0;
}

/* the on hover styling */
.onHover {
  position: relative;
}

.moreBtn {
  text-align: center;
  background-color: transparent;
  border: none;
  color: darkseagreen;
  margin: 0;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  font-family: 'Poppins', sans-serif;
}

  
/* middle styling */
.jumbotron {
  background-color:thistle !important; 
  margin-top: 45px;
  height: 250px;
  border: 0px;
  color: white;
  padding-left: 50px;
  font-family: 'Poppins', sans-serif;
  }

.img {
  list-style-type: none;
  display: inline-flex;
  padding-left: 250px;
}

.html {
  padding: 20px;
}

.java {
  padding: 30px;
}

.js {
  padding: 15px;
}

.mysql {
  padding: 10px;
}

.post {
  padding: 20px;
}

.python {
  padding: 12px;
  padding-top: 25px;
}

.react {
  padding: 10px;
}
/* work grid styling */
.work-grid {
    margin-top: 43px;
    margin-bottom: 10px;
  }
  
  #work {
    margin: 20px;
  }
  
  #work:hover {
    border: solid 5px darkseagreen;
  }

  .ul.list {
    list-style-type: none;
    display: inline-flex;
    padding: 0px;
    margin: 0px;
  }

  .modal {
    background-color: transparent;
  }

/* footer nav */
.footer-nav {
    background-color: darkseagreen; 
    padding: 30px;
    margin-top: 20px;
    min-width: 100%;
    margin: 0;
    font-family: 'Poppins', sans-serif;
    color: white;
  }

  
.p {
    margin-top: 15px;
}

img.work-images {
    margin-bottom: 10px;
    border: 3px solid darkseagreen;
}
body {
  color: darkseagreen;
  font-family: 'Poppins', sans-serif;
  background-repeat: no-repeat;
  background-attachment: fixed; 
}


